"use client";

import { useState, useRef, useEffect } from 'react';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { ScrollArea } from '@/components/ui/scroll-area';

interface Message {
  id: string;
  text: string;
  isUser: boolean;
  timestamp: Date;
}

const predefinedResponses: Record<string, string> = {
  // Salutations
  'bonjour': 'Bonjour ! Je suis l\'assistant virtuel de WZDIAB. Comment puis-je vous aider aujourd\'hui ?',
  'salut': 'Salut ! Je suis là pour répondre à vos questions sur nos services médicaux.',
  'bonsoir': 'Bonsoir ! Comment puis-je vous assister ce soir ?',
  
  // Rendez-vous
  'rendez-vous': 'Pour prendre rendez-vous, cliquez sur "Rendez-vous" dans le menu ou visitez notre page de réservation. Vous pouvez choisir votre spécialité, votre médecin et votre créneau horaire.',
  'rdv': 'Pour prendre rendez-vous, cliquez sur "Rendez-vous" dans le menu ou visitez notre page de réservation. Vous pouvez choisir votre spécialité, votre médecin et votre créneau horaire.',
  'réserver': 'Vous pouvez réserver une consultation en ligne 24h/24. Sélectionnez votre spécialité, choisissez un médecin et un créneau disponible.',
  'annuler': 'Vous pouvez annuler votre rendez-vous jusqu\'à 24h avant la consultation. Contactez-nous au 023 45 67 89 ou par email.',
  
  // Horaires
  'horaires': 'Nos horaires d\'ouverture sont : Lundi-Vendredi 8h-18h, Samedi 8h-14h, Dimanche fermé.',
  'ouvert': 'Nous sommes ouverts du lundi au vendredi de 8h à 18h, et le samedi de 8h à 14h.',
  'fermé': 'Nous sommes fermés le dimanche. Du lundi au vendredi : 8h-18h, Samedi : 8h-14h.',
  
  // Livraison
  'livraison': 'Nous livrons vos médicaments dans toute l\'Algérie ! Livraison standard 2-3 jours, urgente 24h. Frais variables selon la wilaya.',
  'médicaments': 'Commandez vos médicaments en ligne et recevez-les chez vous. Paiement à la livraison, coursiers professionnels.',
  'coursier': 'Nos coursiers sont formés pour manipuler les médicaments en toute sécurité. Suivi par SMS et appel téléphonique.',
  'frais': 'Les frais de livraison varient selon votre wilaya : 150 DA (grandes villes), 200 DA (standard), 400 DA (zones éloignées).',
  
  // Spécialités
  'spécialités': 'Nous proposons : Médecine générale, Cardiologie, Dermatologie, Gynécologie, Pédiatrie, Neurologie, Orthopédie, Ophtalmologie, ORL, Psychiatrie.',
  'médecins': 'Nous travaillons avec plus de 500 médecins spécialisés dans toute l\'Algérie. Tous sont certifiés et expérimentés.',
  'cardiologue': 'Nos cardiologues : Dr. Karim Boumediene, Dr. Amina Cherif, Dr. Yacine Meziane. Prenez rendez-vous en ligne.',
  'généraliste': 'Médecins généralistes disponibles : Dr. Ahmed Benali, Dr. Fatima Khelifi, Dr. Mohamed Saidi.',
  
  // Contact
  'contact': 'Contactez-nous : Téléphone 023 45 67 89, Email contact@wzdiab.dz, ou utilisez ce chat 24h/24.',
  'téléphone': 'Notre numéro de téléphone : 023 45 67 89 (disponible aux heures d\'ouverture).',
  'email': 'Notre email : contact@wzdiab.dz - Nous répondons sous 24h.',
  
  // Urgences
  'urgence': 'En cas d\'urgence médicale, appelez le 14 (SAMU) ou rendez-vous aux urgences de l\'hôpital le plus proche.',
  'samu': 'Numéro d\'urgence SAMU : 14. Pour les urgences non vitales, consultez nos médecins en ligne.',
  
  // Paiement
  'paiement': 'Paiement à la livraison pour les médicaments. Pour les consultations, règlement sur place ou en ligne.',
  'prix': 'Les prix des consultations varient selon la spécialité. Les médicaments sont facturés au prix pharmacie + frais de livraison.',
  
  // COVID
  'covid': 'Nous respectons toutes les mesures sanitaires. Téléconsultations disponibles. Port du masque obligatoire.',
  'masque': 'Le port du masque est obligatoire dans nos locaux pour votre sécurité et celle de nos équipes.',
  
  // Assurance
  'assurance': 'Nous acceptons la plupart des assurances maladie. Vérifiez avec votre assureur avant la consultation.',
  'sécurité sociale': 'Nous travaillons avec la sécurité sociale algérienne. Apportez votre carte Chifa.',
  
  // Aide générale
  'aide': 'Je peux vous aider avec : prise de rendez-vous, livraison de médicaments, informations sur nos services, horaires, contact.',
  'services': 'Nos services : Consultations médicales, Livraison de médicaments, Téléconsultations, Suivi médical, Assistant virtuel 24h/24.'
};

const getResponse = (message: string): string => {
  const lowerMessage = message.toLowerCase().trim();
  
  // Recherche exacte
  if (predefinedResponses[lowerMessage]) {
    return predefinedResponses[lowerMessage];
  }
  
  // Recherche par mots-clés
  for (const [key, response] of Object.entries(predefinedResponses)) {
    if (lowerMessage.includes(key)) {
      return response;
    }
  }
  
  // Réponse par défaut
  return 'Je suis là pour vous aider ! Vous pouvez me demander des informations sur :\n\n• Prise de rendez-vous\n• Livraison de médicaments\n• Nos horaires et services\n• Contact et urgences\n\nPosez-moi votre question plus précisément.';
};

export default function Chatbot() {
  const [isOpen, setIsOpen] = useState(false);
  const [messages, setMessages] = useState<Message[]>([
    {
      id: '1',
      text: 'Bonjour ! Je suis votre assistant virtuel WZDIAB. Comment puis-je vous aider aujourd\'hui ?',
      isUser: false,
      timestamp: new Date()
    }
  ]);
  const [inputMessage, setInputMessage] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  const sendMessage = async () => {
    if (!inputMessage.trim()) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      text: inputMessage,
      isUser: true,
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputMessage('');
    setIsTyping(true);

    // Simulate typing delay
    setTimeout(() => {
      const botResponse: Message = {
        id: (Date.now() + 1).toString(),
        text: getResponse(inputMessage),
        isUser: false,
        timestamp: new Date()
      };

      setMessages(prev => [...prev, botResponse]);
      setIsTyping(false);
    }, 1000);
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      sendMessage();
    }
  };

  const quickQuestions = [
    'Comment prendre rendez-vous ?',
    'Quels sont vos horaires ?',
    'Livraison de médicaments',
    'Contacter un médecin'
  ];

  const handleQuickQuestion = (question: string) => {
    setInputMessage(question);
    setTimeout(() => sendMessage(), 100);
  };

  return (
    <>
      {/* Chat Button */}
      <div className="fixed bottom-6 right-6 z-50">
        <Button
          onClick={() => setIsOpen(!isOpen)}
          className="h-14 w-14 rounded-full shadow-lg hover:shadow-xl transition-all duration-200"
          size="sm"
        >
          {isOpen ? (
            <span className="text-xl">✕</span>
          ) : (
            <span className="text-xl">💬</span>
          )}
        </Button>
      </div>

      {/* Chat Window */}
      {isOpen && (
        <div className="fixed bottom-24 right-6 z-50 w-96 max-w-[calc(100vw-2rem)]">
          <Card className="shadow-2xl border-2">
            <CardHeader className="pb-3">
              <CardTitle className="flex items-center space-x-2">
                <span className="text-lg">🤖</span>
                <span>Assistant WZDIAB</span>
                <div className="ml-auto flex items-center space-x-1">
                  <div className="w-2 h-2 bg-green-500 rounded-full"></div>
                  <span className="text-xs text-muted-foreground">En ligne</span>
                </div>
              </CardTitle>
            </CardHeader>
            <CardContent className="p-0">
              {/* Messages */}
              <ScrollArea className="h-80 px-4">
                <div className="space-y-4">
                  {messages.map((message) => (
                    <div
                      key={message.id}
                      className={`flex ${message.isUser ? 'justify-end' : 'justify-start'}`}
                    >
                      <div
                        className={`max-w-[80%] rounded-lg px-3 py-2 text-sm ${
                          message.isUser
                            ? 'bg-primary text-primary-foreground'
                            : 'bg-muted text-muted-foreground'
                        }`}
                      >
                        <p className="whitespace-pre-line">{message.text}</p>
                        <p className="text-xs opacity-70 mt-1">
                          {message.timestamp.toLocaleTimeString('fr-FR', {
                            hour: '2-digit',
                            minute: '2-digit'
                          })}
                        </p>
                      </div>
                    </div>
                  ))}
                  
                  {isTyping && (
                    <div className="flex justify-start">
                      <div className="bg-muted text-muted-foreground rounded-lg px-3 py-2 text-sm">
                        <div className="flex space-x-1">
                          <div className="w-2 h-2 bg-current rounded-full animate-bounce"></div>
                          <div className="w-2 h-2 bg-current rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                          <div className="w-2 h-2 bg-current rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                        </div>
                      </div>
                    </div>
                  )}
                  <div ref={messagesEndRef} />
                </div>
              </ScrollArea>

              {/* Quick Questions */}
              {messages.length === 1 && (
                <div className="px-4 py-2 border-t">
                  <p className="text-xs text-muted-foreground mb-2">Questions fréquentes :</p>
                  <div className="flex flex-wrap gap-1">
                    {quickQuestions.map((question, index) => (
                      <Button
                        key={index}
                        variant="outline"
                        size="sm"
                        className="text-xs h-7"
                        onClick={() => handleQuickQuestion(question)}
                      >
                        {question}
                      </Button>
                    ))}
                  </div>
                </div>
              )}

              {/* Input */}
              <div className="p-4 border-t">
                <div className="flex space-x-2">
                  <Input
                    value={inputMessage}
                    onChange={(e) => setInputMessage(e.target.value)}
                    onKeyPress={handleKeyPress}
                    placeholder="Tapez votre message..."
                    className="flex-1"
                    disabled={isTyping}
                  />
                  <Button 
                    onClick={sendMessage} 
                    size="sm"
                    disabled={!inputMessage.trim() || isTyping}
                  >
                    <span className="text-sm">📤</span>
                  </Button>
                </div>
                <p className="text-xs text-muted-foreground mt-2 text-center">
                  Assistant virtuel disponible 24h/24
                </p>
              </div>
            </CardContent>
          </Card>
        </div>
      )}
    </>
  );
}
